<template>
    <a-card hoverable style="width: 85%; margin: 0 auto; max-width: 500px">
        <template #cover>
            <a-image
                style="margin: 0 auto"
                :src="picUrl"
                fallback="data:image/png;base64,"
            />
        </template>

        <a-card-meta
            :title="song_info.name"
            :description="
                song_info.artists[0].name + '「' + song_info.album.name + '」'
            "
        >
        </a-card-meta>
    </a-card>

    <a-divider>{{song_info.name}}</a-divider>

    <a-skeleton active :loading="!lyric"> 
    <div style="text-align: center;white-space:pre-line;">
        {{ lyric.replace(/\[[\d]+\:[\d]+\.[\d]+\]/gm, '') }}
    </div>
    </a-skeleton>
</template>
<script>
export default {
    exports: {
        name: "songinfo",
    },
    data() {
        return {};
    },
    methods: {
        get_lyric(song_id) {
            console.log("Get lyric");
            window.opener.postMessage(
                {
                    type: "get_lyric",
                    song_id: song_id,
                },
                "*"
            );
        },
    },
    props: ["song_info", "lyric", "picUrl"],
    components: {},
};
</script>
